import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css'
import '../style/custom.css'
import config from '../../data/SiteConfig'
import IntlProvider from '../util/intlProvider'
import MainLayout from '../util/mainLayout'
import NavBar from '../components/Navbar'
import AresSdk from '../components/AresSdk'
import AboutPlasma from '../components/AboutPlasma'
import AboutBigchaindb from '../components/AboutBigchaindb'
import AboutRNG from '../components/AboutRNG'
import FooterSuitePage from '../components/FooterSuitePage/Footer'
const Section = ({ children, className }) => <div className={className}>{children}</div>
 
const Main = ({ langKey, messages }) => (
  <IntlProvider langKey={langKey} messages={messages}>
    <div>
      <NavBar langKey={langKey} color="Sara" />
      <MainLayout langKey={langKey} theme="mainsuite" footerTheme="footer">
      <Section>
          <AresSdk />
        </Section>
        <Section>
          <AboutPlasma />
        </Section>
        <Section>
          <AboutBigchaindb />
        </Section>
        <Section>
          <AboutRNG />
        </Section>
        <FooterSuitePage config={config} langKey={langKey} />
      </MainLayout>
    </div>
  </IntlProvider>
)
 
export default Main


