import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const AboutPlasma = () => (
  <div className="container p-3 p-md-5 pt-0 pb-0 main-content">
    <div className="About-Plasma-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40">
            <Col  md={12}>
                <h1 className="display-3 headline" style={{ color: '#001ada', fontWeight: 'bold' }}>
                <FormattedMessage id="AboutPlasma.title" />
                </h1>
            </Col>
            <Col  md={4}>
            <img src="/images/img1.png" width="100%" height="100%" alt="" />
            </Col>
            <Col  md={4}>
            <p style={{ color: '#001ada', marginTop: '20px', fontSize: '1rem' }}>
                <FormattedMessage id="introduce.title" />
            </p>
            </Col>
            <Col  md={4}>
            <div className="button-maintain-suite">
                <Button className="button-2">
                    <FormattedMessage id="checkout.title" />                                                               
                </Button>
            </div>
            </Col>
        </Row>
    </Container>
    </div>
  </div>
)

export default AboutPlasma



