import React from 'react'
import { Container, Row, Col, Button } from 'reactstrap'
import { FormattedMessage } from 'react-intl'
import './index.css'


const AresSdk = () => (
  <div className="container p-3 p-md-5 main-content mt-80">
    <div className="Ares-Suite-SDK-container" style={{position: 'relative', overflow: 'hidden'}}>
      <Container>
        <Row className="show-grid pt-40 pb-40 pr-25 pl-25">
          <Col  md={12}>
            <h1 className="display-3 headline" style={{ color: '#fff', fontWeight: 'bold' }}>
              <FormattedMessage id="AresSuiteSDK.title" />
            </h1>
          </Col>
          <Col  md={4} className="pr-0">
            <img src="/images/img1.png" width="100%" height="100%" alt="" />
          </Col>
          <Col  md={4}>
            <p style={{ color: '#ffffff', marginTop: '20px', fontSize: '1rem' }}>
             <FormattedMessage id="introduce.title" />
            </p>
          </Col>
          <Col  md={4}>
            <div className="button-maintain-suite">
              <Button className="button-1">
                <FormattedMessage id="get-started.title" />
              </Button>
            </div>
          </Col>
        </Row>
      </Container>
    </div>
  </div>
)

export default AresSdk



